<template>
  <div>

    <div class="text-left">
      <b-link @click="returnToAlbum()">
        <b-icon style="margin-top: 5px" icon="chevron-left"></b-icon>
        {{this.artistName}}
      </b-link>
    </div>

    <div v-if="this.artistAlbumSongs.album">
      <b-img center v-bind="albumArtProps" alt="Center image"></b-img>
      <h5>{{this.artistAlbumSongs.album.name}}</h5>
      <h6>{{this.artistAlbumSongs.artist.name}}</h6>
      <small>{{this.artistAlbumSongs.album.genre}} - {{this.artistAlbumSongs.album.year}}</small>
    </div>

    <b-row style="margin-top: 10px">
      <b-col class="text-right">
        <b-button pill style="width: 150px" variant="outline-primary" @click="playAlbum(0)">
          <b-icon icon="play"></b-icon>
          Play
        </b-button>
      </b-col>
      <b-col class="text-left">
        <b-button pill style="width: 150px" variant="outline-primary" @click="playAlbumShuffled()">
          <b-icon icon="shuffle"></b-icon>
          Shuffle
        </b-button>
      </b-col>
    </b-row>

    <b-table selectable select-mode="single" hover class="text-left" foot-clone style="margin-top: 10px"
             ref="songsTableRef"
             :items="artistAlbumSongs.songs"
             :fields="songsTable.fields"
             @row-selected="rowSelected"
             thead-class="d-none">

      <template #cell(index)="data">
        <template v-if="data.rowSelected">
          <b-icon icon="disc" animation="spin"></b-icon>
        </template>
        <template v-else>
          <div>{{data.index + 1}}</div>
        </template>
      </template>

      <template #cell(name)="data">
        <div class="text-left">{{data.item.name}}</div>
      </template>

      <template #cell(timeInSeconds)="data">
        <b-form-text class="text-right" disabled>{{convertSecondsToMinutesAndSeconds(data.item.duration)}}</b-form-text>
      </template>

      <template #foot(index)="data">
        <div></div>
      </template>

      <template #foot(name)="data">
        <div></div>
      </template>

      <template #foot(timeInSeconds)="data">
        <div class="text-right">
          <small>{{totalTime}}</small>
        </div>
      </template>

    </b-table>
  </div>
</template>

<script>

import {BIcon, BIconChevronLeft, BIconPlay, BIconShuffle, BIconDisc} from 'bootstrap-vue'
import {eventBus} from "@/main";
import {HTTP_CLIENT} from "@/http/axios-config";

export default {
  name: 'Songs',
  components: {
    BIcon,
    BIconChevronLeft,
    BIconPlay,
    BIconShuffle,
    BIconDisc
  },
  data() {
    return {
      songsTable: {
        fields: ['index', 'name', 'timeInSeconds']
      },
      totalTime: '',
      artistAlbumSongs: {},
      artistName: '',
      albumArtProps: {blank: true, blankColor: '#D0D0D0', width: 125, height: 125, class: 'm1'}
    }
  },
  methods: {
    returnToAlbum() {
      this.$router.push({name: "Albums", params: {artistId: this.artistAlbumSongs.artist.id}})
    },
    indexSelected(data) {
      console.log(this.$route.params.artistId);
      return '1';
    },
    rowSelected(items) {
      let idx = this.artistAlbumSongs.songs
          .map((item, index) => {return {id: item.id, idx: index}})
          .filter((item) => item.id === items[0].id)[0].idx;
      this.playAlbum(idx);
    },
    playAlbum(songIndex) {
      this.$store.commit('setArtistAlbumSongs', {
        artistAlbumSongs: this.artistAlbumSongs,
        playingSongIdx: songIndex
      });
      eventBus.$emit('player-load')
    },
    playAlbumShuffled() {
      //TODO
      // min = Math.ceil(min);
      // max = Math.floor(max);
      // return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    convertSecondsToMinutesAndSeconds(seconds) {
      return Math.floor(seconds / 60) + ':' + ('0' + Math.floor(seconds % 60)).slice(-2);
    }
  },
  created() {
    // if (this.$store.state.playingSongIdx >= 0) {
    //     this.$refs.selectableTable.selectRow(2);
    // }
    let albumId = this.$route.params.albumId;
    HTTP_CLIENT.get('/library/songs/album/' + albumId).then(response => {
      this.artistAlbumSongs = response.data;
      this.artistName = this.artistAlbumSongs.artist.name
      this.totalTime = this.artistAlbumSongs.songs.length + ' songs, ' + this.artistAlbumSongs.album.totalTime;
      if (this.artistAlbumSongs.album) {
        let src = HTTP_CLIENT.defaults.baseURL + "/image/open/?albumId=" + this.artistAlbumSongs.album.id
        this.albumArtProps = {src: src, width: 125, height: 125}
      }
    })
  }
}
</script>