<template>
  <div>

    <div class="text-left">
      <b-link to="/library">
        <b-icon style="margin-top: 5px" icon="chevron-left"></b-icon>
        Library
      </b-link>
    </div>

    <b-list-group class="pqd-list-group">

      <b-list-group-item class="pqd-list-group-item">
        <h3>Artists</h3>
      </b-list-group-item>

      <b-list-group-item class="pqd-list-group-item"
                         button v-for="artist of this.$store.getters.getArtists" :key="artist.name" @click="onArtistSelected(artist.id)">
<!--        <b-img v-bind:blank="true" width="25" height="25" blank-color="#D0D0D0" rounded="circle" alt="HEX color image"></b-img>-->
        {{artist.name}}
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>

import {BIcon, BIconChevronLeft} from 'bootstrap-vue'
import {HTTP_CLIENT} from "@/http/axios-config"

export default {
  name: 'Artists',
  components: {
    BIcon,
    BIconChevronLeft
  },
  methods: {
    onArtistSelected(artistId) {
      this.$router.push({name: 'Albums', params: {artistId: artistId}});
    }
  },
  created() {
    HTTP_CLIENT.get('/library/artists/').then(response => {
      this.$store.commit('setArtists', response.data.artists);
    }).catch(function(error) {
      alert(error)
    });
  }
}
</script>