<template>
  <div id=app>
<!--    <img alt="Vue logo" src="./assets/logo.png" />-->
    <b-container fluid="sm">
      <Audio/>
      <b-row>
        <b-col>
          <router-view></router-view>
        </b-col>
      </b-row>
      <MiniPlayer/>
    </b-container>
  </div>
</template>

<script>

import store from "@/store/index";
import VueRouter from "vue-router";

import Audio from "@/components/library/Audio";
import Library from './components/library/Library'
import Artists from './components/library/Artists'
import Albums from './components/library/Albums'
import Songs from './components/library/Songs'
import Player from './components/library/Player'
import MiniPlayer from './components/library/MiniPlayer'

const routes = [
  { path: '/library', name: 'Library', component: Library },
  { path: '/library/artists', name: 'Artists', component: Artists },
  { path: '/library/artists/:artistId', name: 'Albums', component: Albums },
  { path: '/library/artists/albums/:albumId', name: 'Songs', component: Songs },
  { path: '/library/player/:songId', name: 'Player', component: Player }
]

export default {
  name: 'App',
  store: store,
  router: new VueRouter({
    routes
  }),
  components: {
    Audio,
    Library,
    Artists,
    Albums,
    Songs,
    Player,
    MiniPlayer
  }
}

</script>
